.hero-container {
    /* height: 90vh; */
    display: flex;
    align-items: center;
    position: relative;
}

.hero-content h2 {
    font-size: 3.8rem;
    font-weight: 600;
    line-height: 5rem;
    margin-bottom: 1rem;
}

.hero-content p {
    width: 80%;
    font-size: 1rem;
    font-weight: 400;
    line-height: 2rem;
}

.hero-img {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    transition: transform 0.3s ease;
}

.hero-img:hover {
    transform: translate(-0.5rem);
}

.logo {
    width: 550px;
}

.highlight {
    position: relative;
    display: inline-block;
  }
  
.highlight::before {
    content: '';
    position: absolute;
    width: 50%;
    height: 100%;
    background-color: #76B4F0; 
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scaleX(0);
    transition: transform 0.3s ease-in-out;
    z-index: -1; 
}

.highlight:hover::before {
    transform: translate(-50%, -50%) scaleX(2);
}

.hero-container::after{
    content: " ";
    width: 40.125rem;
    height: 40.125rem;
    border-radius: 30.125rem;
    background-color: #729dc7;
    position: absolute;
    z-index: -9999;
    top: -2rem;
    left: -4rem;
    filter: blur(225px);
}

@media (max-width: 1025px) {
    .hero-content h2 {
        font-size: 2.5rem;
        line-height: 3.5rem;
    }

    .hero-content p {
        font-size: 0.9rem;
        line-height: 1.6rem;
    }

    .hero-img > img {
        width: 30rem;
    } 
}

@media (max-width: 768px) {
    .hero-container {
        flex-direction: column;
    }

    .hero-content h2 {
        margin-top: 3rem;
    }

    .hero-content h2,
    .hero-content p {
        width: auto;
        text-align: center;
    }

    .hero-img {
        margin: 2rem 0 2rem 0;
    }

    .hero-container::after{
        content: " ";
        width: 18rem;
        height: 18rem;
        top: 2rem;
        left: 1rem;
    }
}

